body{
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 1.4em;
    background: #f5f5f5;
    color: #4d4d4d;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 300;
}
.addTodos, .displaytodos, .card{
    margin: 0 auto;
    max-width: 960px;
}
.addTodos{
    padding: 100px 0 0;
}
.addTodos h1 {
    top: -155px;
    width: 100%;
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
    -webkit-text-rendering: optimizeLegibility;
    -moz-text-rendering: optimizeLegibility;
    text-rendering: optimizeLegibility;
}
.todo-input{
    position: relative;
    margin: 0;
    width: 80%;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    color: purple;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    padding: 16px 16px 16px 60px;
    border: 1px solid purple;
    outline: none;
    background: rgba(0, 0, 0, 0.003);
    border-radius: 4px;
    box-shadow: inset 0 -2px 1px rgb(0 0 0 / 3%);
}
.add-btn{
    font-size: 20px;
    height: 69px;
    background-color: purple;
    color: #fff;
    outline: none;
    border: 1px solid purple;
    border-radius: 4px;
    margin-right: 6px;
    padding: 15px 16px 15px 16px;
    box-shadow: inset 0 -2px 1px rgb(0 0 0 / 3%);
    cursor: pointer;
    margin-left: 10px;
}
.btn{
    cursor: pointer;
    color: purple;
    border: none;
    background-color: transparent;
}
.selected{
    border:  1px solid purple;
}
.list{
    list-style-type: none;
    width: 300px;
    padding-left: 0;
}
.list li, textarea{
    border: 1px solid purple;
    background-color: rgba(0, 0, 0, 0.003);
    color: purple;
    font-size: 22px;
    padding: 10px;
    border-radius: 6px;
    margin: 10px 0;
}
.list-edit{
    padding-left: 0;
    list-style-type: none;
}
.active, .complete, .cancel{
    color: purple;
    border: 1px solid rgb(231, 86, 231);
    background-color: rgb(231, 86, 231);
    cursor: pointer;
    margin-right: 6px;
    padding: 6px 10px;
}